@use "vars";

ul.constraints-list {
    &, ul {
        padding: 0;
        list-style-type: none;
    }

    .constraint-row {
        white-space: nowrap;
        &::before {
            content: "";
            display: inline-block;
        }

        .constraint-row-left {
            float: left;
        }
        .constraint-row-right {
            float: right;
        }
        // display: flex;
        // justify-content: space-between;
        // flex-wrap: wrap;

    }

    .section-title {
        margin: 0.5em 0;
        font-weight: vars.$font-weight-heavy;

        &::before {
            content: "â–¾ ";
            // This can't be the right way to do this, can it? The intent is to
            // have a drop-down tree menu.
        }
    }

    form > div {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}
